import React from 'react'
import { MapTo } from '@adobe/aem-react-editable-components';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const Title = styled.h1`
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    /* font-size: 60px; */
    font-size: ${props => props.titleSize};
    line-height: 90px;
    /* color: #e0e0e0; */
    color: ${props => props.titleColor};

  @media (max-width: 650px) {
    font-size: 48px;
    line-height: 0px;
  }
`;
const Paragraph = styled.p`
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #e0e0e0;
    color: ${props => props.paragraphColor};
`;


export const TitleSection = ({ title, size, paragraph, titleColor, paragraphColor }) => {
    return (
        <div>
            <Title titleColor={titleColor} titleSize={size} >{title}</Title>
            <Paragraph paragraphColor={paragraphColor}>{paragraph}</Paragraph>
        </div>
    )
}

TitleSection.propTypes = {
    title: PropTypes.string,
    titleColor: PropTypes.string,
    size: PropTypes.string,
    paragraph: PropTypes.string,
    paragraphColor: PropTypes.string,
}

export default MapTo('reactapp/components/title-section')(TitleSection);
